<template>
  <div class="contens ">
    <h1><span></span>租车费用有哪些？</h1>
    <ul class="List">
      <li>
        <div class="imgDiv">
          <img src="../../../assets/feedbacksFlow/m9.png" alt="">
        </div>
      </li>
      <li>
        <div class="header2">
          <div class="imgh1"><img src="../../../assets/feedbacksFlow/m3s.png" alt=""></div>
          押金和租赁费用
          <div class="imgh2"><img src="../../../assets/feedbacksFlow/m3.png" alt=""></div>
        </div>
      </li>
      <li>
        <div class="textCent">
          <p><i></i>一般包括押金和租赁费用，押金还车后可退。</p>
          <p><i></i>部分商家支持免押金，芝麻信用{{zmxy}}分以上可使用免押服务。</p>
          <p><i></i>租赁费用根据用户租赁时长计算，可在订单详情内查看。</p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import http from '../../../service/http.js'
  export default {
    components:{
    },
    data() {
      return {
        active: 0,
        zmxy:640
      }
    },
    mounted(){
      this.creditScore();
    },
    methods:{
      creditScore(){
        http.get("scooter/creditScore")
          .then(res => {
             console.log(res)
            if(res.resultCode==1){
              this.zmxy=res.data;
            }
          })
      }
    },
  }
</script>

<style scoped lang="scss">
  .Fixs{
    position: fixed;
    left: 0;
    top: 88px;
    height: 100%;
    width: 100%;
  }
  .List{
    padding-bottom: 80px;
    background: #ffffff;
    li{
      width: 100%;
      padding: 0 20px;
      box-sizing: border-box;
    }
  }
  .textCent{
    display: inline-block;
    margin: 0 auto;
    p{
      font-size:26px;
      font-family:PingFangSC;
      font-weight:400;
      color:rgba(72,72,72,1);
      line-height:36px;
      margin-bottom: 20px;
      text-align: left;
      position: relative;
      padding-left: 26px;

      i{
        width:8px;
        height:8px;
        display: inline-block;
        background:rgba(165,165,165,1);
        border-radius: 4px;
        position: absolute;
        left: 0px;
        top: 14px;
      }
    }


  }
  .header2{
    height: 48px;
    display:inline-block;
    margin: 0 auto;
    margin-top: 52px;
    margin-bottom: 40px;
    font-size:34px;
    font-family:PingFangSC;
    font-weight:600;
    color:rgba(51,51,51,1);
    line-height:48px;
    position: relative;
    div{
      width:102px;
      height:12px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .imgh1{
      position: absolute;
      left: -122px;
      top: 0;
    }
    .imgh2{
      position: absolute;
      right: -128px;
      top: 0;
    }
  }
  .imgDiv{
    height: 220px;
    width: 300px;
    margin: 0 auto;
    margin-top: 42px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .contens{
    margin-top: 58px;
    h1{
      width:100%;
      height:48px;
      font-size:34px;
      font-family:PingFangSC;
      font-weight:600;
      color:rgba(51,51,51,1);
      line-height:48px;
      padding-left: 76px;
      position: relative;
      text-align: left;
      span{
        position: absolute;
        left: 52px;
        top: 8px;
        width:8px;
        height:32px;
        background:rgba(255,141,89,1);
      }
    }
  }
</style>



